
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import Collapse from '@/components/Collapse/Collapse'
import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Tab,TabItem,Collapse,slide
},
data() {
return {
tabIndex: 0,
params:{},
info:{
HZXM:"",
HZNL:"",
HZKS:"",
BCH:"",
JZLSH:"",
},//信息
drugListInfo:[]
}
},
created(){
this.params = this.$route.params;
this.getInfo();
},
methods: {
slide(index){
this.$refs.slide.refresh();
this.$refs.slide.goToPage(index);
},
getInfo(){

this.$ajax.post('',{
'MSH.1':'doctorsOrder',
'MSH.2':'yz',
'yzzid':this.params.yzzid,//医嘱主ID
'ny': this.params.ny,//医嘱年月
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res)
if(res.info && res.drugListInfo){
this.info = res.info;
this.drugListInfo = res.drugListInfo;

}
}).catch(error => {
console.log(error);

});
},
}

}



